import React, { useState } from "react";

import * as card from "./styled/card";

const CARD_OFFSET = 8;
const SCALE_FACTOR = 0.06; //  

/**
 *  StackedCards Component Type
 */
interface StackedCardsProps {
  title: string;
  chip: string;
  src: string; //    prop
  images: { [key: string]: { src: string; alt: string } }; //    
}

export const StackedCards: React.FC<StackedCardsProps> = (props) => {
  const { title, chip, src, images } = props;
  const imageKeys = Object.keys(images); //      
  const [cards, setCards] = useState(imageKeys); //  
  const [clickRight, setClickRight] = useState(false);

  //       
  const moveToEnd = (from: number) => {
    setCards((prevCards) => {
      const updatedCards = [...prevCards];
      const movedCard = updatedCards.splice(from, 1);
      updatedCards.push(movedCard[0]);
      return updatedCards;
    });
  };

  //   
  const handleClick = () => {
    setClickRight(true);
    setTimeout(() => {
      moveToEnd(0);
      setClickRight(false);
    }, 300); // 0.3     
  };

  return (
    <card.article>
      <card.TitleWrapper>
        <card.TitleArea>
          <h4>ABOUT Life</h4>
          <h1>{title}</h1>
        </card.TitleArea>
        <card.ChipArea>
          <card.Chip>{chip}</card.Chip>
        </card.ChipArea>
      </card.TitleWrapper>

      <card.wrapperStyle>
        <card.CardWrap>
          {cards.map((imageName, index) => (
            <card.CardItem
              key={imageName}
              style={{
                cursor: index === 0 ? "grab" : "auto",
                zIndex: imageKeys.length - index,
              }}
              animate={{
                top: index * -CARD_OFFSET,
                scale: 1 - index * SCALE_FACTOR,
                rotate: index === 0 ? (clickRight ? -45 : 0) : 0, //    
              }}
              onClick={handleClick}
            >
              <card.Image
                src={src + images[imageName].src} //     
                alt={images[imageName].alt} //      
              />
            </card.CardItem>
          ))}
        </card.CardWrap>
      </card.wrapperStyle>
    </card.article>
  );
};
