import React, { useEffect, useRef, useState } from "react";
import { Map, MapMarker, CustomOverlayMap } from "react-kakao-maps-sdk";
import * as kakaoMap from "./styled/kakaoMap";

//  
const imagePath = process.env.PUBLIC_URL + "/common/images/";

export const KakaoMap = () => {
  const position = {
    lng: 126.9186859271572,
    lat: 37.55715071108653,
  };

  return (
    <kakaoMap.MapWrapper>
      <kakaoMap.InformArea>
        <kakaoMap.InformContent>
             31 <br />
            * 2F  
        </kakaoMap.InformContent>
      </kakaoMap.InformArea>
      <kakaoMap.MapArea>
        <Map
          //    (,)
          center={position}
          level={3}
          style={{
            width: "100%",
            height: "31.25rem",
            borderRadius: "1.25rem",
            backgroundColor: "#e0e0e0",
            boxShadow: ".1875rem .625rem 1.125rem rgba(0, 0, 0, 0.2)",
          }}
        >
          {/*    */}
          <MapMarker
            position={position}
            image={{
              src: imagePath + "marker_custom.png",
              size: {
                width: 64,
                height: 69,
              },
              options: {
                offset: {
                  x: 25,
                  y: 69,
                }, //  .       .
              },
            }}
          />

          <CustomOverlayMap position={position} yAnchor={1}>
            <kakaoMap.SpeechBubble>
              <a
                href="https://place.map.kakao.com/1719950135"
                target="_blank"
                rel="noreferrer"
              >
                <span className="title"></span>
              </a>
            </kakaoMap.SpeechBubble>
            ;
          </CustomOverlayMap>
        </Map>
      </kakaoMap.MapArea>

      <kakaoMap.OnToWayArea>
        <kakaoMap.ItemArea>
          <kakaoMap.Round>  </kakaoMap.Round>
          <kakaoMap.Describe>
            <ul>
              <li>
                 <span className="line_2">2</span> &nbsp;
                <span className="line_name_center"></span>&nbsp;
                <span className="line_name_airport"></span>  |  
                 8
              </li>
              <li>
                 <span className="line_6">6</span>  |    15
              </li>
              <li>
                 <span className="line_6">6</span>&nbsp;
                <span className="line_2">2</span>  |    16
              </li>
            </ul>
          </kakaoMap.Describe>
        </kakaoMap.ItemArea>
        <kakaoMap.ItemArea>
          <kakaoMap.Round>  </kakaoMap.Round>
          <kakaoMap.Describe>
            <ul>
              <li>
                <span className="bus_blue"></span> : N73()
              </li>
              <li>
                <span className="bus_green"></span> : 7016 | 7711 | 7737
              </li>
              <li>
                <span className="bus_green"></span> : 09
              </li>
            </ul>
          </kakaoMap.Describe>
        </kakaoMap.ItemArea>

        <kakaoMap.ItemArea>
          <kakaoMap.Round>  </kakaoMap.Round>
          <kakaoMap.Describe>
            <ul>
              <li>   : 250 </li>
            </ul>
          </kakaoMap.Describe>
        </kakaoMap.ItemArea>
      </kakaoMap.OnToWayArea>
    </kakaoMap.MapWrapper>
  );
};
