import React from "react";
import { Variants } from "framer-motion";
import * as blink from "./styled/blink";

/** * Framer motion Variants */
const blinkVariants: Variants = {
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,
    transition: {
      when: "beforeChildren",
      staggerChildren: 0.2,
    },
  },
};

const textVariants = (index: number): Variants => ({
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      delay: Math.random() * index * 0.1,
      duration: Math.random() * index * 0.2,
      repeat: Math.random() * index,
      repeatType: "reverse",
    },
  },
});

/**
  @title Blink Components
*/
export const BlinkText = () => {
  const title = {
    first: ``,
    second: ``,
    third: `  `,
    fourth: `:)`,
  };

  return (
    <blink.BlinkArea
      variants={blinkVariants}
      initial="hidden"
      animate="visible"
    >
      <blink.TitleArea>
        {title.first.split("").map((char, index) => (
          <blink.BlinkingChar
            key={index}
            variants={textVariants(index)}
            custom={textVariants}
          >
            {char}
          </blink.BlinkingChar>
        ))}

        {/*  */}
        <blink.Space />

        {title.second.split("").map((char, index) => (
          <blink.BlinkingChar
            key={index}
            variants={textVariants(index)}
            custom={textVariants}
          >
            {char}
          </blink.BlinkingChar>
        ))}

        {/*  */}
        <blink.Space />

        {title.third.split("").map((char, index) => (
          <blink.BlinkingChar
            key={index}
            variants={textVariants(index)}
            custom={textVariants}
          >
            {char}
          </blink.BlinkingChar>
        ))}
      </blink.TitleArea>

      <blink.ContentArea>
        <blink.Content>  </blink.Content>
        <blink.Content>  </blink.Content>
        <blink.Content>  , .</blink.Content>
      </blink.ContentArea>
      <blink.NameArea>
        <blink.Name>
          <span>  </span>  <span></span>
        </blink.Name>
        <blink.Name>
          <span>  </span>  <span></span>
        </blink.Name>
      </blink.NameArea>
    </blink.BlinkArea>
  );
};
